.search-result_msg-to-user {
    text-align: center;
    padding: 20px 0;
}

.search-result_txt-muted {
    color: #9e9c9c;
}

.dev-list {
    width: 100%;
    margin: 0;
    padding: 0;
    list-style-type: none;
    color: var(--cor-black);
}

.dev-list-item {
    padding: 20px;
    background: white;
    display: flex;
    border-bottom: 1px solid #d5d8df;
    border-radius: 4px;
}
  
.dev-details {
    padding-left: 20px;
    border-left: 1px solid #eee;
    flex: 1;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.dev-details p {
    margin: 0;
    min-width: 0;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.container-scroll {
    border-radius: 10px;
    overflow: none;
    position: relative;
    /* height: 55vh; */
    border: 1px solid #ccc;
    background-color: white;
}

.content-scroll {
    overflow: auto;
    position: absolute;
    width: 100%;
    max-height: 100%;
}

.dev-btn-more-details {
    padding-left: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.dev-name-more-details {
    width: 100%;
    display: flex;
}

@media (min-width: 769px) {
    .search-result-content {
        width: 60%;
    }
}

@media (max-width: 440px) {
    .dev-name-more-details {
        display: block;
    }
}